
import { defineComponent, ref } from 'vue';


export default defineComponent({
  name: "HelpInput",
  setup(){
    const textValue = ref("(例) 〇〇医院\n(例) 梅屋　崇\n(例) あま市\n(例) 000-00-0000\n(例) 111-11-1111");
    const checkValue = ref(false)
    const textArray = ref(["", "", "", "", ""]);
    const checkDate = ref(false);
    const dateArray = () => {
      if(checkDate.value) {
        let d = new Date();
        let year = d.getFullYear().toString();
        let mounth = (d.getMonth() + 1).toString();
        let day = d.getDate().toString();
        return [year, mounth, day];
      } else {
        return ["", "", ""];
      }
    }
    const checkText = () => {
      let strsN = textValue.value.split('\n');
      let strsR = textValue.value.split('\r\n');
      let strs = strsN.length > strsR.length ? strsN : strsR;
      for (let i = 0; i < 5; i++) {
        textArray.value[i] = i < strs.length ? strs[i] : "";
      }
      return textArray.value;
    };
    const dataObj = () => {
      if(checkValue.value) {
        return {textArray: checkText(), dateArray: dateArray()};
      } else {
        return {textArray: ["", "", "", "", ""], dateArray: ["", "", ""]};
      }
    }
    checkText();
    return {
      textValue,
      checkValue,
      checkText,
      textArray,
      checkDate,
      dateArray,
      dataObj,
    }
  }
})
