
import { questionSet } from "@/store/questionnaire.summary";
import { defineComponent, ref } from "vue";
import HelpInput from "@/components/HelpInput.vue";

export default defineComponent({
  name: "PrintView2",
  components: {
    HelpInput,
  },
  props: {
    resultNumber: {
      type: Number,
      default: 2,
    },
    medicine: {
      type: String,
      default: "",
    },
    addition: {
      type: String,
      default: "",
    },
    answers: {
      type: Array,
      default: () => {
        [];
      },
    },
    riskL: {
      type: Array,
      default: () => ["", "", ""],
    },
    riskH: {
      type: Array,
      default: () => ["", ""],
    },
  },
  setup(props) {
    const typeName = location.search == "?type=ba2" ? "ba2" : "omicron";
    const resultList = questionSet[typeName].result;
    const rslt = resultList[props.resultNumber];
    const infs = ref(["","","","",""]);
    const date = ref(["", "", ""]);

    return {
      rslt,
      typeName,
      infs,
      date,
    };
  },
});
